@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Oswald&display=swap");

html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  flex: 1 0 auto;
  & p:last-child {
    margin-bottom: 2rem;
  }
  background: linear-gradient(
    138deg,
    rgb(26, 30, 36) 20%,
    rgb(40, 44, 52) 50%,
    rgb(26, 30, 36) 80%
  );
}

.recharts-responsive-container {
  margin: 0rem auto 1rem auto;
  transition: all 0.1s linear;
  overflow: visible;
}

/*

  tooltip

*/

.recharts-default-tooltip {
  width: 200px;
  background: rgba(33, 37, 43, 0.95);
  text-align: left;
  padding: 0.9rem 1rem;
  box-shadow: 0.1em 0.1em 5em 0em rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc;
  border-radius: 0.4rem;
  /* transform: perspective(300px) rotateY(-2deg); */
  & .recharts-tooltip-label {
    padding: 0;
    margin: 0 auto 0.4rem 0;
    text-align: center;
    font-weight: bold;
  }
  & .recharts-tooltip-item-list {
    margin: 0 auto;
    padding: 0 0.5rem;
    & .recharts-tooltip-item {
      list-style: none;
      margin-bottom: 0.15rem;
      margin-right: 0.5rem;
      font-size: 0.8rem;
      & .recharts-tooltip-item-name {
        width: 50px;
      }
      & .recharts-tooltip-item-value {
        color: white;
        text-align: right;
      }
    }
  }
}

.recharts-surface {
  /* margin-left: -10%; */
  /* transform: perspective(300px) rotateY(-2deg); */
}

/*

  legend and brush

*/

.recharts-legend-wrapper {
  /* transform: perspective(0px) rotateY(0deg); */
}
.recharts-legend-item-text,
.recharts-tooltip-item-name,
.recharts-surface path {
  user-select: none;
  font-family: Oswald;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.recharts-default-legend li {
  cursor: pointer;
}
.recharts-brush {
  & > rect:first-child {
    fill: rgb(26, 30, 36);
    fill-opacity: 1;
  }
  & .recharts-brush-slide {
    fill: rgb(40, 44, 52);
    fill-opacity: 1;
  }
}

.desactivated {
  display: none;
}

/* recharts lines */

.recharts-line:not(.desactivated) {
  & path {
    transition: stroke-width 0.2s linear, stroke 0.2s linear;
    /* stroke-width: 2; */
  }
  &:has(+ .recharts-active-dot:hover) {
    & path {
      stroke-width: 10;
    }
  }
  &:hover {
    & path {
      stroke-width: 10;
    }
  }
}

.recharts-reference-line text {
  font-size: 0.7rem;
  fill: rgb(26, 30, 36);
  text-transform: none;
  font-style: italic;
  display: none;
  & tspan {
    fill: white;
  }
}

/**/

/*

color scale of gravity

*/

.one-yellow {
  background: #aaa;
}
.one-orange {
  background: #aaa;
}
.one-red {
  background: #aaa;
}
.one-black {
  background: #aaa;
}

/*

dashboard

*/

.dashboard {
  display: flex;
  flex-wrap: wrap;

  & .graph {
    min-width: 60%;
    flex-grow: 1;
  }
  & .carto {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    /* padding: 0 2.5rem; */
    margin-bottom: 2rem;
    /* min-width: 400px; */
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    /* padding: 0 2.5%; */
    & .info {
      color: white;
      margin: auto;
      & .date {
        display: inline-block;
        font-family: Oswald;
        color: rgb(170, 170, 170);
        font-weight: bold;
        font-size: 1.5rem;
        margin: 0 0 1.5rem 0;
        vertical-align: sub;
        padding-left: 1.5rem;
      }
    }
    & .carto-legend {
      /* align-self: center; */
      color: white;
      font-weight: bold;
      font-size: 0.8rem;
      /* width: 6rem; */
      margin: auto;
      display: flex;
      /* text-align: left; */
      & .carto-legend-title {
        margin-right: 1rem;
      }
      & .carto-legend-element {
        & .carto-legend-scale {
          vertical-align: top;
          cursor: mark;
          display: inline-block;
          vertical-align: center;
          /* width: 2rem; */
          width: 2rem;
          height: 2rem;
          line-height: 2rem;

          /* border-radius: 1rem; */
        }
      }
    }
  }
}

/*

carto svg

*/

.france-svg {
  /* width: 90%; */
  /* min-width: 100%; */
  /* min-height: 100%; */
  min-height: 320px;
  width: 300px;
  margin: auto;
  & g {
  }
  & .tooltip {
    position: absolute;
    text-align: center;
    padding: 0.5rem;
    background: #ffffff;
    color: #313639;
    border: 1px solid #313639;
    border-radius: 8px;
    pointer-events: none;
    font-size: 1.3rem;
  }
  & text {
    text-anchor: middle;
    font-size: 1rem;
    font-weight: bold;
    fill: white;
    stroke: rgb(33, 37, 43);
    stroke-width: 1px;
    stroke-linecap: round;
    stroke-linejoin: round;
    cursor: pointer;
  }

  & path {
    cursor: pointer;
    filter: url(#erode);
    stroke: gray;
    stroke-width: 1px;
    stroke-linecap: round;
    stroke-linejoin: round;
    draw: 10s;

    /* transition: all 0.3s; */
    &.selected {
      /* filter: url(#blur); */
      filter: url(#blur) url(#erode);
      stroke: yellow;
      stroke-width: 4px;
      /* stroke-dasharray: 2; */
      transition: all 0.3s;
    }
  }
}

.idf-svg {
  min-height: 250px;
}

/* */
@keyframes roller-out {
  0% {
    transform: rotateX(0deg); /* Position de départ */
  }
  100% {
    transform: rotateX(360deg); /* Position d'arrivée */
  }
}

@keyframes roller-in {
  0% {
    transform: rotateX(360deg); /* Position de départ */
  }
  100% {
    transform: rotateX(0deg); /* Position d'arrivée */
  }
}

.counter {
  display: flex;
  align-items: center;
  min-height: 3.2rem;
  & .city,
  & .concentration {
    font-size: 1.4rem;
    text-transform: capitalize;
    opacity: 0%;
    transition: all 1s;
  }

  & .city {
    flex-basis: 55%;
    text-align: right;
    opacity: 100%;
    white-space: nowrap;
  }
  & .concentration {
    padding: 0.5rem 0.75rem;
    border-radius: 0.5rem;
    display: inline-block;
    margin-left: 2rem;
    opacity: 100%;
  }
  & .fade-in {
    opacity: 100%;
    transition: opacity 1s linear;
  }
  & .fade-out {
    opacity: 0%;
    transition: opacity 0.5s ease-in-out;
  }
  & .roll-in {
    animation: roller-in 1s ease-in-out;
  }
  & .roll-out {
    animation: roller-out 1s ease-in-out;
  }
}

/* element styling */
text {
  font-family: Roboto;
}
a {
  color: white;
  font-family: Oswald;
}

h1,
h2,
h3 {
  color: #eeeeee;
  font-family: Oswald;
  padding: 0 1rem;
  margin: 1em auto;
}
p {
  line-height: 1.4rem;
  margin: 1em auto;
  max-width: 720px;
  color: white;
  padding: 0em 1rem;
  text-align: left;
}
footer {
  background-color: rgb(26, 30, 36);
  border-top: 1px solid rgb(40, 44, 52);
  & div {
    padding: 1rem;
    max-width: 480px;
    margin: auto;
    margin-bottom: 1rem;
    color: gray;
  }
}

.reset {
  background: none;
  font-size: 0.9rem;
  color: white;
  font-family: Roboto;
  display: inline-block;
  font-style: normal;
  cursor: pointer;
  margin-left: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  border: 3px solid rgb(102, 102, 102);
  transition: all 0.2s linear;
  &:hover {
    border: 3px solid white;
  }
}

/* mediaqueries */

/* CSS styles for screens with a width greater than 600px */
@media screen and (min-width: 600px) {
  .recharts-reference-line text {
    font-size: 0.7rem;
    display: block;
  }
}

/* CSS styles for screens with a width greater than 800px */
@media screen and (min-width: 800px) {
  .recharts-reference-line text {
    font-size: 0.9rem;
    display: block;
  }
  .dashboard {
    & .graph {
    }
    & .carto {
      flex-grow: 0;
    }
    & header {
    }
  }
  .france-svg {
    /* min-width: 400px;
    min-height: 600px; */
  }
}
